<template>
    <v-card flat>
      <v-container fluid>
        <div class="w-break">
          <div class="subTitle"> (nem)</div>
          <v-card-text>
            <h2 class="font-color-shamrock">{{ nemBalance }} xem</h2>
          </v-card-text>
          <div v-if="selectMosaic">
            <div class="subTitle"> ({{ selectMosaic.namespaceId }})</div>
            <v-card-text>
              <h2 class="font-color-shamrock">{{ selectMosaic.amount }} {{ selectMosaic.name }}</h2>
            </v-card-text>
            <v-card-actions>
              <v-checkbox
                label=""
                :input-value="selectMosaic.transferable"
                color="success"
              ></v-checkbox>
              <v-checkbox
                label=""
                :input-value="selectMosaic.supplyMutable"
                color="success"
              ></v-checkbox>
            </v-card-actions>
          </div>
            <v-flex justify-center>
            <v-select
              :items="mosaics"
              label=""
              v-model="selectMosaic"
              prepend-icon="view_quilt"></v-select>
            </v-flex>
          <v-form v-model="valid" ref="form" lazy-validation>
            <v-text-field
              box
              label=""
              v-model="name"
              :rules="nameRules"
              :counter="16"
              required
            ></v-text-field>
          </v-form>
          <v-subheader></v-subheader><v-card-text>{{ walletItem.account.address.value }}</v-card-text>
          <v-subheader></v-subheader><v-card-text>{{ pairKey.publicKey }}</v-card-text>
          <v-subheader></v-subheader><v-btn color="pink" class="white--text" @click="showPrivateKey"></v-btn>
          <v-subheader>QR</v-subheader>
          <qriously v-model="qrValue" :size="200"></qriously>
          <br>
          <!--  -->
          <dialogConfirm v-bind:dialogVal="isShowDialog"
                         titleVal=""
                         v-bind:messageVal="pairKey.privateKey"
                         v-on:dialog-confirm-event-tap-positive="tapPositive"></dialogConfirm>
        </v-flex>
        </div>
      </v-container>
    </v-card>
</template>

<script>
  // import dbWrapper from '@/js/local_database_wrapper'
  import nemWrapper from '@/js/nem_wrapper'
  import DialogConfirm from '@/components/DialogConfirm'
  import { mapGetters } from 'vuex'

  export default {
    data: () => ({
      valid: true,
      selectMosaic: null,
      qrValue: '',
      isShowDialog: false,
      nameRules: [
        value => !!value || '',
        value => (value && value.length <= 16) || ''
      ],
      descriptionRules: [
        value => (value.length <= 1024) || ''
      ]
    }),
    computed: {
      ...mapGetters('Nem', ['walletItem', 'pairKey', 'nemBalance', 'mosaics', 'transactionStatus', 'isLoading'])
    },
    components: {
      'dialogConfirm': DialogConfirm
    },
    mounted () {
      this.reloadItem()
    },
    props: {
      name: {
        type: String,
        default: ''
      }
    },
    watch: {
      mosaics (after, before) {
        console.log(after)
        console.log(before)
        this.reloadItem()
      }
    },
    methods: {
      reloadItem () {
        if (this.mosaics.length > 0) {
          this.selectMosaic = this.mosaics[0]
        }
        this.qrValue = nemWrapper.getJSONInvoiceForQRcode(2, 1, this.name, this.walletItem.account.address.value, 0, '')
      },
      showPrivateKey () {
        this.isShowDialog = true
      },
      tapPositive (message) {
        this.isShowDialog = false
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



